	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"New Home For Sunlog","date":"14 Sep 2002","slug":"/2002-09-14-new-home-for-sunlog/","tags":"Y2002"},"html":"<p>As <a href=\"http://www.sunflyer.ch/site.php?sid=7c7eb78f0cfd01dcafe4fb11514a4666&page=1\">reported</a> by its former maintainer, the German PHP/MySQL weblogging package <a href=\"http://www.sunlog.org\">Sunlog</a> has a <a href=\"http://www.fiene.tv/\">new maintainer</a>,  and a new home at <a href=\"http://bytecamp.net/\">bytecamp.net</a> (who also host PapaScott). I nearly chose Sunlog for PapaScott. It's a nice package, and it's good to see that work on it will continue.</p>"},"next":{"fields":{"title":"Blogroll on a Stick","date":"14 Sep 2002","slug":"/2002-09-14-blogroll-on-a-stick/"}},"previous":{"fields":{"title":"Trading Allies for Votes","date":"16 Sep 2002","slug":"/2002-09-16-trading-allies-for-votes/"}}},{"node":{"fields":{"title":"Blogroll on a Stick","date":"14 Sep 2002","slug":"/2002-09-14-blogroll-on-a-stick/","tags":"Y2002"},"html":"<p>As a service to myself, I've added a <a hef=\"javascript:window.sidebar.addPanel('papascott panel', 'http://www.papascott.de/sidebar.php','')\">link</a> to create a Mozilla sidebar for my blogroll. It's the beginning of a personal blog reader and aggregator which I can use in whatever copy of Mozilla I happen to be using.</p>"},"next":{"fields":{"title":"No, I Fling Her","date":"13 Sep 2002","slug":"/2002-09-13-no-i-fling-her/"}},"previous":{"fields":{"title":"New Home For Sunlog","date":"14 Sep 2002","slug":"/2002-09-14-new-home-for-sunlog/"}}},{"node":{"fields":{"title":"No, I Fling Her","date":"13 Sep 2002","slug":"/2002-09-13-no-i-fling-her/","tags":"Y2002"},"html":"<p>In case anyone is wondering, in order to find the Monty Python sketch <a href=\"http://www.montypython.net/scripts/interest.php\">Interesting People</a>, in which Graham Chapman throws a cat into a bucket of water, it is sufficient to do a Google search on <a href=\"http://www.google.com/search?q=cat bucket fling her&amp;start=0&amp;start=0\">cat bucket fling her</a>.</p>"},"next":{"fields":{"title":"Munich Calling","date":"11 Sep 2002","slug":"/2002-09-11-munich-calling/"}},"previous":{"fields":{"title":"Blogroll on a Stick","date":"14 Sep 2002","slug":"/2002-09-14-blogroll-on-a-stick/"}}}],"pathPrefix":"","first":false,"last":false,"index":847,"pageCount":1101}}

